import { AuthenticatedReq, OpenReq } from "Utils/Axios";

export default class BusinessApi {
  public static getAllBusiness = async () => {
    return await AuthenticatedReq(`/business/all`, "GET");
  };

  public static getDefaultBusiness = async () => {
    return await AuthenticatedReq(`/business/default-business`, "GET");
  };

  public static getGeneralSettings = async () => {
    return await AuthenticatedReq(`/business`, "GET");
  };

  public static putGeneralSettings = async (businessId: string, data: any) => {
    return await AuthenticatedReq(`/business/${businessId}`, "PUT", { data });
  };

  public static getWABasicSettings = async () => {
    return await AuthenticatedReq(`/whatsapp/business-profile`, "GET");
  };

  public static saveWABasicSettings = async (data: any) => {
    return await AuthenticatedReq(`/whatsapp/profile-update`, "POST", { data });
  };

  public static getWAChatBots = async () => {
    return await AuthenticatedReq(`/chatbots`, "GET");
  };

  public static getTeamMembers = async (
    search: string = "",
    pageNo: number = 1,
    limit: number = 20
  ) => {
    return await AuthenticatedReq(
      `/team-members?search=${search}&page=${pageNo}&limit=${limit}`,
      "GET"
    );
  };

  public static getUserInvites = async () => {
    return await AuthenticatedReq(`/invitation/all`, "GET");
  };

  public static sendUserInvite = async (data: any) => {
    return await AuthenticatedReq(`/invitation`, "POST", { data });
  };

  public static deleteUserInvite = async (invitationId: number) => {
    return await AuthenticatedReq(`/invitation/${invitationId}`, "DELETE");
  };

  public static getLeadDataFields = async () => {
    return await AuthenticatedReq(`/lead-data-fields`, "GET");
  };

  public static getLeadDataField = async (ldfId: number) => {
    return await AuthenticatedReq(`/lead-data-fields/edit/${ldfId}`, "GET");
  };

  public static createLeadDataField = async (data: any) => {
    return await AuthenticatedReq(`/lead-data-fields/`, "POST", { data });
  };

  public static updateLeadDataField = async (editLdfId: number, data: any) => {
    return await AuthenticatedReq(
      `/lead-data-fields/update/${editLdfId}`,
      "PUT",
      { data }
    );
  };

  public static deleteLeadDataField = async (deleteLdfId: number) => {
    return await AuthenticatedReq(
      `/lead-data-fields/delete/${deleteLdfId}`,
      "DELETE"
    );
  };

  public static getLeadDistributionRules = async (
    pageNo: number = 1,
    limit: number = 20
  ) => {
    return await AuthenticatedReq(
      `/lms-rules?page=${pageNo}&limit=${limit}`,
      "GET"
    );
  };

  public static createLeadDistributionRule = async (data: any) => {
    return await AuthenticatedReq(`/lms-rules`, "POST", { data });
  };

  public static deleteLeadDistributionRule = async (deleteLdrId: number) => {
    return await AuthenticatedReq(`/lms-rules/${deleteLdrId}`, "DELETE");
  };

  public static getWaTemplateMessages = async () => {
    return await AuthenticatedReq(`/wa-templates`, "GET");
  };

  public static getWaTemplateMessage = async (editWaTemplateId: number) => {
    return await AuthenticatedReq(`/wa-templates/${editWaTemplateId}`, "GET");
  };

  public static syncWaTemplateMessage = async () => {
    return await AuthenticatedReq(`/wa-templates/sync-templates`, "GET");
  };

  public static createWaTemplateMessage = async (data: any) => {
    return await AuthenticatedReq(`/wa-templates/create-template`, "POST", {
      data,
    });
  };

  public static updateWaTemplateMessage = async (
    editWaTemplateId: number,
    data: any
  ) => {
    return await AuthenticatedReq(
      `/wa-templates/update-template-form/${editWaTemplateId}`,
      "PUT",
      { data }
    );
  };

  public static updateWaTemplateComponents = async (
    editWaTemplateId: number,
    data: any
  ) => {
    return await AuthenticatedReq(
      `/wa-templates/update/${editWaTemplateId}`,
      "PUT",
      { data }
    );
  };

  public static deleteWaTemplateMessage = async (
    deleteWaTemplateId: number
  ) => {
    return await AuthenticatedReq(
      `/wa-templates/delete/${deleteWaTemplateId}`,
      "DELETE"
    );
  };

  public static getWaStatus = async () => {
    return await AuthenticatedReq(`/platforms/wa/status`, "GET");
  };

  public static getWaHealthReport = async () => {
    return await AuthenticatedReq(`/whatsapp-usage/health-report`, "GET");
  };

  public static getWaMonthlyReport = async () => {
    return await AuthenticatedReq(`/whatsapp-usage/monthly-utilization`, "GET");
  };

  public static downloadWaMonthWiseReport = async (data: any) => {
    return await AuthenticatedReq(
      `/whatsapp-usage/download-consumed-report`,
      "POST",
      {
        data,
        responseType: "blob",
      }
    );
  };

  public static getRangeUtilization = async (from: Date, to: Date) => {
    return await AuthenticatedReq(
      `/whatsapp-usage/utilization?from=${from.getTime()}&to=${to.getTime()}`,
      "GET"
    );
  };

  public static getSetupInfo = async () => {
    return await AuthenticatedReq(`/platforms/sm-connected-platform`, "GET");
  };

  public static getBusinessFBPages = async () => {
    return await AuthenticatedReq(`/fb/facebook-pages`, "GET");
  };

  public static saveFBUserData = async (businessId: number, data: any) => {
    return await AuthenticatedReq(`/fb/pages/${businessId}`, "POST", {
      data,
    });
  };

  public static saveFBPageData = async (data: any) => {
    return await AuthenticatedReq(`/fb/account`, "POST", {
      data,
    });
  };

  public static getIgAccountData = async () => {
    return await AuthenticatedReq(`/ig/fetchData`, "GET");
  };

  public static saveIgAccountData = async (data: any) => {
    return await AuthenticatedReq(`/ig/account`, "POST", {
      data,
    });
  };

  public static unlinkSocialMediaAccount = async (data: any) => {
    return await AuthenticatedReq(`/platforms/unlinkAccount/`, "DELETE", {
      data,
    });
  };

  public static getLiUserPages = async (data: any) => {
    return await AuthenticatedReq(`/setup/linkedin/pages`, "POST", { data });
  };

  public static setLinkedinBusiness = async (data: any) => {
    return await AuthenticatedReq(`/setup/linkedin/setup-page`, "POST", {
      data,
    });
  };

  public static getCountryProvinces = async (country: string) => {
    return await OpenReq(`/countries/${country}.json`, "GET");
  };
}
